import {localLoad, localSave} from "@/utils/StorageUtil";
import {Skill} from "@/model/skill-sim";


const state = () => ({
    customSKills: [],
})

const getters = {
    num: state => state.customSKills.length,
}

const actions = {
    async updateCustomSkill({ commit }, payload) {
        await commit('updateCustomSkill', payload)
        await commit('saveCustomSkills')
    },
    async deleteCustomSkill({ commit }, payload) {
        await commit('deleteCustomSkill', payload)
        await commit('saveCustomSkills')
    },
    async loadCustomSkills({ commit }) {
        await commit('loadCustomSkills')
    },
    async upMoveCustomSkills({ commit }, payload) {
        await commit('upMoveCustomSkills', payload)
        await commit('saveCustomSkills')
    },
    async downMoveCustomSkills({ commit }, payload) {
        await commit('downMoveCustomSkills', payload)
        await commit('saveCustomSkills')
    },
}

const mutations = {
    updateCustomSkill(state, { name, skill }) {
        const index = state.customSKills.findIndex(item => item.name === name)
        if (index === -1) {
            state.customSKills.push({ name, skill })
        } else {
            state.customSKills[index].skill = skill
        }
    },
    deleteCustomSkill(state, { name }) {
        const index = state.customSKills.findIndex(item => item.name === name)
        state.customSKills.splice(index, 1)
    },
    saveCustomSkills(state) {
        localSave('customSkills', state.customSKills)
    },
    loadCustomSkills(state) {
        const customSkills = localLoad('customSkills')
        if (customSkills && customSkills.length > 0) {
            state.customSKills = customSkills.map(obj => {
                return {
                    name: obj.name,
                    skill: new Skill(obj.skill)
                }
            })
        }
    },
    upMoveCustomSkills(state, { skills }) {
        skills.forEach(skill => {
            const index = state.customSKills.findIndex(item => item.name === skill)
            if (index < 0 || index === 0) {
                return
            }
            // 将当前技能与前一个技能交换位置
            const temp = state.customSKills[index - 1]
            state.customSKills[index - 1] = state.customSKills[index]
            state.customSKills[index] = temp
        })
        // 刷新技能列表
        state.customSKills.push('')
        state.customSKills.pop()
    },
    downMoveCustomSkills(state, { skills }) {
        // 从后往前遍历
        for (let i = skills.length - 1; i >= 0; i--) {
            const skill = skills[i]
            const index = state.customSKills.findIndex(item => item.name === skill)
            if (index < 0 || index === state.customSKills.length - 1) {
                continue
            }
            // 将当前技能与后一个技能交换位置
            const temp = state.customSKills[index + 1]
            state.customSKills[index + 1] = state.customSKills[index]
            state.customSKills[index] = temp
        }
        // 刷新技能列表
        state.customSKills.push('')
        state.customSKills.pop()
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
}